.navbar {
  padding: 20px 25px;
  margin: 0 auto;
}

.navbar-collapse {
  margin: 0 auto;
  justify-content: center;
  align-items: flex-start;
}

.search-area {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}

.searchbox button {
  border: 1px solid #101D42;
}

.searchbox button:hover {
  background-color: #101D42;
  color: #ffffff;
}

